import React, { Suspense, lazy } from 'react';
import { Route, Routes as RouterRoutes} from 'react-router-dom';
import { InvalidObject } from '@redhat-cloud-services/frontend-components/InvalidObject';

import { Bullseye, Spinner } from '@patternfly/react-core';

const OopsPage = lazy(() =>
  import(/* webpackChunkName: "OopsPage" */ './Routes/OopsPage/OopsPage')
);
const NoPermissionsPage = lazy(() =>
  import(
    /* webpackChunkName: "NoPermissionsPage" */ './Routes/NoPermissionsPage/NoPermissionsPage'
  )
);
const AuthFactors = lazy(() => 
  import(
    /* webpackChunkName: "Authentication-Factors"*/ './Routes/Authentication-Policy/Authentication-Factors'
  )
);

const IdpIntegration = lazy (() =>
  import(
     /* webpackChunkName: "Identity-Provider-Integration"*/ './Routes/Authentication-Policy/Identity-Provider-Integration'
  )
)

/**
 * the Switch component changes routes depending on the path.
 *
 * Route properties:
 *      exact - path must match exactly,
 *      path - https://prod.foo.redhat.com:1337/insights/advisor/rules
 *      component - component to be rendered when a route has been chosen.
 */
export const Routes = () => (
  <Suspense
    fallback={
      <Bullseye>
        <Spinner />
      </Bullseye>
    }
  >
    <RouterRoutes>
        <Route path="oops" element={<OopsPage/>} />
        <Route path="no-permissions" element={<NoPermissionsPage/>} />
        <Route path="authentication-factors/*" element={<AuthFactors/>} />
        <Route path="identity-provider-integration/*" element={<IdpIntegration/>}/>
      {/* Finally, catch all unmatched routes */}
        <Route path="*" element={<OopsPage/>} />
    </RouterRoutes>
  </Suspense>
);
